  padding: 0;
  margin: 2em 0;

  li {
    display: inline-block;
    cursor: pointer;
    border-radius: 6px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    min-width: 32px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    list-style: none;
    float: left;
    border: 1px solid #d9d9d9;
    background-color: #fff;
    margin-right: 8px;

    span {
      padding: 8px 10px;
    }

    a {
      color: #666;
      padding: 8px 10px;
      text-decoration: none;
    }

    &:hover {
      -webkit-transition: all .3s ease;
      transition: all .3s ease;
      border-color: #2db7f5;
    }
    &:hover a {
      -webkit-transition: all .3s ease;
      transition: all .3s ease;
      color: #2db7f5;
    }

    &.active {
      background-color: #2db7f5;
      border-color: #2db7f5;
    }

    &.active span {
      color: #fff;
    }

    &.disabled {
      cursor: not-allowed;
    }

    &.disabled:hover {
      border-color: #d9d9d9;
    }
  }
}

