//Code Highlight
pre {
  margin: 1.2em 0;
  position: relative;

  &:after {
    position: absolute;
    top: 0;
    right: 0;
    color: #c8c8c8;
    text-align: right;
    font-size: 0.75em;
    padding: 5px 10px 0;
    line-height: 15px;
    height: 15px;
    font-style: italic;
    font-weight: 600;
  }

  &[data-lang=html]:after {
    content: 'HTML';
  }

  &[data-lang=css]:after {
    content: 'CSS';
  }

  &[data-lang=javascript]:after,
  &[data-lang=js]:after {
    content: 'JavaScript';
  }

  &[data-lang=typescript]:after,
  &[data-lang=ts]:after {
    content: 'TypeScript';
  }

  &[data-lang=php]:after {
    content: 'PHP'
  }

  &[data-lang=java]:after {
    content: 'Java'
  }

  &[data-lang=python]:after,
  &[data-lang=py]:after {
    content: 'Python'
  }

  &[data-lang=ruby]:after {
    content: 'Ruby'
  }

  &[data-lang=sql]:after {
    content: 'SQL'
  }

  &[data-lang=json]:after {
    content: 'JSON'
  }

  &[data-lang=markdown]:after,
  &[data-lang=md]:after {
    content: 'Markdown'
  }

  &[data-lang=bash]:after,
  &[data-lang=shell]:after {
    content: 'Shell'
  }

  &[data-lang=diff]:after,
  &[data-lang=patch]:after {
    content: 'Diff'
  }
}

